
import { defineComponent, PropType } from 'vue';
import embedType from '../util/embed-type';

export default defineComponent({
    name: 'Embed',
    data() {
        return {
            title: '',
            description: '',
            thumbnail: ''
        }
    },
    props: {
        embed: Object as PropType<embedType>,
        order: Number
    }
})
